@use 'base/variables' as v;
@use 'base/mixins' as m;

.video {
    position: relative;
    max-height: 70rem;
    min-height: 20rem;
    overflow: hidden;


    .overlay {
        position: absolute;

        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
  
    }

    video {
        width: 900px;
        display: block;
        object-fit: cover;
        @include m.desktop {
            align-items: center;
            width: 100%;
            object-fit: cover;
        }
    }



}


.contenido-video {
    z-index: 10 ;
    h2 {
        color: v.$blanco;
        font-size: 7rem;
        margin: 0;
    }
    p {
        background-color: v.$rosa;
        display: inline-block;
        padding: 1rem 2rem;
        color: v.$blanco;
        font-weight: 700;
        font-size: 2.4rem;
        margin: 0;
    }
}