@use 'variables' as v;


html {
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-padding-top: 0rem;
}
*, 
*:before,
*:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    background: linear-gradient(rgba(14, 167, 37, 0.3), rgb(255, 255, 255));

    &.fijar-body {
        overflow: hidden;
    }
    &.body-scroll {
        padding-top: 8.6rem;
    }
}
.grids{
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.cuadrito{
    border-radius: 25px;
    border: 5px solid rgb(50, 178, 202);
    padding: 10px;
    margin: 0;
    background-color:  rgb(50, 178, 202, 0.5);
z-index: 100;

}
.video-container 
{
  top: 0%;
  left: 0%;
  height: 400px;
  width: 100%;
  overflow: auto;

}
video.fillWidth {
  width: 100% !important;
  object-fit: cover;
}
p {
    font-size: 2rem;
    color: v.$negro;
    line-height: 1.5;
}
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}


.ml15 {
    font-weight: 800;
  
    text-transform: uppercase;
    letter-spacing: 0.2em;
  }
  
  .ml15 .word {
    display: inline-block;
    line-height: 1em;
   
  }

 .tope{
    margin-top: 100px !important;
    font-size: 100px;

 
 }



.ml6 {
    position: relative;
    font-weight: 900;
    font-size: 3.3em;
  }
  
  .ml6 .text-wrapper {
    position: relative;
    display: inline-block;
    padding-top: 0.2em;
    padding-right: 0.05em;
    padding-bottom: 0.1em;
    overflow: hidden;
  }
  
  .ml6 .letter {
    display: inline-block;
    line-height: 1em;
  }


a {
    text-decoration: none;
}
h1, h2, h3 {
    margin: 0 0 5rem 0;
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}

img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}

