@use 'base/variables' as v;
@use 'base/mixins' as m;

.sobre-festival {
    

    @include m.tablet {
        @include m.grid(2, 5rem);
    }
}

.contenido-festival {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 5rem;
    @include m.tablet {
        margin-top:0;
    }
    h2 {
        margin-bottom: 2rem;

    }
    .fecha {
        color:rgb(237, 149, 33);
        font-weight: 700;
        font-size: 25px !important;
    }
    .respuesta {
        color:rgb(50, 178, 202);
        font-weight: 700;
        font-size: 25px !important;
    }
}